	module.exports = {"data":{"markdownRemark":{"html":"<p>   Few animals roam these portions of dark and dreary Mirkwood. A few forest\ntrolls and the odd mirkwolf account for the majority of the fauna. The flora\nhowever possesses a couple of remarkable and unusal features. Belladona or\nDeadly nightshade dominates the undergrowth. As a result the forest floor\noften appears to be awash with a sea of indigo coloured flowers. The juice of\nthe Belladona berries  can be used as a cosmetic, giving it one of its names\nmeaning, beautiful lady.  However if eaten these berries will turn ones\nstomach and may even result in painful death  - accounting for its second and\nmore sinister name. Redwood Pines dominate the woody vegetation. The Redwood\nPines that grow in this area are of mixed ages. The older more ancient pines\nare rarer, they can be identified not only by their larger size but by their\nirregular rounded crowns. The crowns of these pines are unlike other conifers\nresembling those of their distant broad-leaf relatives. In late spring if you\nlook up high into the trees the male pines bear bright yellow clusters of\nflowers. When the wind blows during these times the air is filled by clouds of\ngolden pollen. An amazing sight indead to behold.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"The Redwoods of South Mirkwood"}}},"pathContext":{"path":"/books/redwoodsofsouthmirkwood"}}

